import React, {
  useState,
  useEffect,
  useMemo,
  useRef,
  useContext,
} from 'react';
import { TransitionsContext } from './Transitions';
import uuid from '../util/uuid';

// unmounted -> leaveed -> entering -> entered -> leaveing -> leaveed -> unmounted
// unmounted
const noop = () => {};

export enum STATUS {
  UNMOUNTED = 'unmounted', // 
  ENTERED = 'entered', // 
  ENTERING = 'entering', // LEAVEEDENTERED
  LEAVEED = 'leaveed', // 
  LEAVEING = 'leaveing', // ENTEREDLEAVEED
};

export type TransitionDuration = {
  enter: number; // entering
  leave: number; // leaveing
};

export interface TransitionProps {
  name?: string; // 
  duration?: number | TransitionDuration; // 
  delay?: number; // enteringleaveing
  animation?: boolean; // 
  unmount?: boolean; // leaveed
  onUnmounted?: () => void; // 
  onEntered?: () => void; // entered
  onEntering?: () => void; // entering
  onLeaveed?: () => void; // leaveed
  onLeaveing?: () => void; // leaveing
  // displaydisplay
  // displayLEAVEEDdisplay: nonestyle
  leaveDisplayNone?: boolean;
  children: React.ReactElement;
  appear?: boolean;
};

type waitForTransitionStartCallback = (...args: any[]) => any;

const durationDefaults = 200;
const delayDefaults = 0;

const Transition: React.FC<TransitionProps> = (props) => {
  const {
    name = 'r',
    duration = durationDefaults,
    delay = delayDefaults,
    animation = false,
    unmount = false,
    onUnmounted = noop,
    onEntered = noop,
    onEntering = noop,
    onLeaveed = noop,
    onLeaveing = noop,
    leaveDisplayNone = false,
    appear = false,
    children,
  } = props;

  // 
  const [_animation, setAnimation] = useState<boolean>(animation);
  // 
  const [status, setStatus] = useState<STATUS>(() => {
    if (_animation) {
      if (appear) {
        return STATUS['ENTERED'];
      } else {
        return STATUS['LEAVEED'];
      }
    } else {
      if (unmount) {
        return STATUS['UNMOUNTED'];
      } else {
        return STATUS['LEAVEED'];
      }
    }
  });
  // 0
  const [interval, setIntervalTime] = useState<number>(0);
  // 
  const firstMount = useRef(true);
  // classNames
  const prevClassNames = useRef(children.props.className || '');
  // styles
  const prevStyle = useRef(children.props.style || '');
  // id
  const id = useRef(uuid());
  const timers = useRef(new Set<NodeJS.Timeout>([]));
  const rAF = typeof window !== 'undefined' && window.requestAnimationFrame;
  const {
    prefix,
    animations,
    collection,
  } = useContext(TransitionsContext);

  /**
   * 
   */
  const waitForTransitionStart = rAF ? (callback: waitForTransitionStartCallback) => {
    rAF(() => {
      rAF(callback);
    })
  } : (callback: waitForTransitionStartCallback) => {
    setTimeout(callback, 50);
  };

  /**
   * 
   */
  const waitTransition = (
    duration: number,
    callback: waitForTransitionStartCallback
  ) => {
    const timer = setTimeout(callback, duration);
    return timer;
  };

  /**
   * STATUS['UNMOUNTED']
   */
  const handleUnmounted = () => {
    if (!firstMount.current) {
      // onUnmounted
      onUnmounted();
    }
    if (_animation) {
      // STATUS['UNMOUNTED']animationtrue
      // STATUS['LEAVEED']
      setStatus(STATUS['LEAVEED']);
    } else {
    }
  };

  /**
   * STATUS['LEAVEED']
   */
  const handleLeaveed = () => {
    if (_animation) {
      // STATUS['LEAVEED']STATUS['ENTERING']
      // waitForTransitionStart
      waitForTransitionStart(() => {
        // ENTERING
        let transitionDelay = delay;
        transitionDelay += interval;
        const timer = waitTransition(transitionDelay, () => {
          setStatus(STATUS['ENTERING']);
        });
        timers.current.add(timer);
      });
    } else {
      // leaveedunmounted -> leaveed
      onLeaveed()
      // Observer
      const { _onLeaveed } = props as any;
      if (_onLeaveed) {
        _onLeaveed();
      }
      // unmounttrueSTATUS['UNMOUNTED']
      if (unmount) {
        setStatus(STATUS['UNMOUNTED']);
      }
    }
  };

  /**
   * STATUS['LEAVEING']
   */
  const handleLeaveing = () => {
    // leaveing
    onLeaveing();
    if (_animation) {
      // STATUS['ENTERING']
      let transitionDelay = delay;
      transitionDelay += interval;
      const timer = waitTransition(transitionDelay, () => {
        setStatus(STATUS['ENTERING']);
      });
      timers.current.add(timer);
    } else {
      // STATUS['LEAVEED']
      let transitionDuration = typeof duration === 'number' ? duration : duration.leave;
      const timer = waitTransition(transitionDuration, () => {
        setStatus(STATUS['LEAVEED']);
      });
      timers.current.add(timer);
    }
  };

  /**
   * STATUS['ENTERED']
   */
  const handleEntered = () => {
    // onEntered
    onEntered();
    if (_animation) {
      // 
    } else {
      // ['LEAVEING']
      let transitionDelay = delay;
      transitionDelay += interval;
      const timer = waitTransition(transitionDelay, () => {
        setStatus(STATUS['LEAVEING']);
      });
      timers.current.add(timer);
    }
  };

  /**
   * STATUS['ENTERING']
   */
  const handleEntering = () => {
    // onEntering
    onEntering();
    if (_animation) {
      // STATUS['ENTERED']
      let transitionDuration = typeof duration === 'number' ? duration : duration.enter;
      const timer = waitTransition(transitionDuration, () => {
        setStatus(STATUS['ENTERED']);
      });
      timers.current.add(timer);
    } else {
      // STATUS['LEAVEING']
      let transitionDelay = delay;
      transitionDelay += interval;
      const timer = waitTransition(transitionDelay, () => {
        setStatus(STATUS['LEAVEING']);
      });
      timers.current.add(timer);
    }
  };

  useEffect(() => {
    timers.current.forEach((timer) => {
      clearTimeout(timer);
    });
    timers.current.clear();
    switch (status) {
      case STATUS['UNMOUNTED']:
        handleUnmounted();
        break;
      case STATUS['LEAVEED']:
        handleLeaveed();
        break;
      case STATUS['LEAVEING']:
        handleLeaveing();
        break;
      case STATUS['ENTERED']:
        handleEntered();
        break;
      case STATUS['ENTERING']:
        handleEntering();
        break;
    }
    // 
    firstMount.current = false;
  }, [status, _animation]);

  useEffect(() => {
    const currentId = id.current;
    if (animations[currentId]) {
      const {
        animation,
        delay,
      } = animations[currentId];
      setIntervalTime(delay);
      setAnimation(animation);
    }
  }, [animations]);

  useEffect(() => {
    setAnimation(animation);
  }, [animation]);


  useEffect(() => {
    if (collection) {
      collection.push(id.current);
    }
  }, []);

  const className = useMemo(() => {
    const classPrefix = prefix ? prefix : name;
    switch (status) {
      case STATUS['LEAVEED']:
        return `${classPrefix}-leaveed`;
      case STATUS['LEAVEING']:
        return `${classPrefix}-leaveing`;
      case STATUS['ENTERED']:
        return `${classPrefix}-entered`;
      case STATUS['ENTERING']:
        return `${classPrefix}-entering`;
    }
  }, [status, prefix, name]);

  const nextStyle = useMemo(() => {
    if (status === STATUS['LEAVEED'] && leaveDisplayNone && !_animation) {
      return {
        display: 'none',
      };
    }
    return null;
  }, [leaveDisplayNone, status, _animation]);

  if (status === STATUS['UNMOUNTED']) {
    return null;
  }

  const nextClassName = prevClassNames.current ?
    `${className} ${prevClassNames.current}`
    : `${className}`;

  return React.cloneElement(React.Children.only(children), {
    className: nextClassName,
    style: {
      ...prevStyle.current,
      ...nextStyle,
    },
  });
};

export default Transition;
